.Container{
    background-color: #428bca;
    width: 310px;
    height: 350px;
    position: relative;
    padding: 20px;
    top: 200px;
    left: 600px;
    box-shadow: 10px 10px black;
}
h1{
    text-align: center;
}
input{
    text-align: center;
    width: 290px;
    height: 30px;
    margin-bottom: 15px; /* Adds space between the input fields */
  padding: 10px; /* Adds some padding inside the input fields */
  width: 100%; /* Make input fields full width */
  box-sizing: border-box;
    /* margin-bottom: 50px; */
}
form{
    /* margin-top: 11px;
     */
     position: relative;
     left: 5px;
     margin-bottom: 200px;
     
}
button{
    text-align: center;
    font-size: 20px;
    background-color: #04AA6D;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    position: relative;
    left: 95px;
}
@media only screen and (max-width: 1000px){
      .Container{
        position: relative;
        left: 300px;
      }

}
@media only screen and (max-width: 800px){
    .Container{
      position: relative;
      left: 200px;
    }

}
@media only screen and (max-width: 670px){
    .Container{
      position: relative;
      left: 100px;
    }

}
@media only screen and (max-width: 470px){
    .Container{
      position: relative;
      left: 30px;
      width: 250px;
    }


}