@media 
only screen and (max-width: 760px),
(min-device-width: 360px) and (max-device-width: 1024px)
{
    table, thead, tbody, th, td, tr { 
		display: block; 
	}
    thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
    tr { border: 1px solid #f2f2f2; }
    td { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #ddd; 
		position: relative;
		padding-left: 50%; 
	}
	
	td:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
	}
    td:nth-of-type(1):before { content: " Name"; }
	td:nth-of-type(2):before { content: "Branch"; }
	td:nth-of-type(3):before { content: "Email"; }
	td:nth-of-type(4):before { content: "Phone no."; }
}